@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@400;700&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-nunito-sans: "Nunito Sans";
  --font-poppins: Poppins;
  --font-roboto: Roboto;
  --font-open-sans: "Open Sans";

  /* font sizes */
  --font-size-base: 16px;
  --font-size-xl: 20px;
  --font-size-21xl: 40px;
  --font-size-6xl: 25px;
  --font-size-lg: 18px;

  /* Colors */
  --white-color: #fff;
  --paragraph-color: #6a6a6a;
  --paragraph-color1: #616161;
  --color-gray-100: #292929;
  --primary-color: #1e1e1e;
  --color-seagreen-100: #007f5f;
  --color-seagreen-200: rgba(0, 127, 95, 0.1);
  --color-darkorange: #ff8b00;
  --color-silver: #c4c4c4;
  --color-whitesmoke: #f3f3f3;

  /* Paddings */
  --padding-base: 16px;
  --padding-16xl: 35px;

  /* border radiuses */
  --br-3xs: 10px;
  --br-mid: 17px;
  --br-5xs: 8px;
  --br-sm: 14px;
}
